<template>
  <div class="kanban">
    <!-- <div class="titleHead">
      <p id="pHead"></p>
      <p id="time">{{ time }}</p>
    </div> -->
    <search-bar @search="search" @transmitYieldName="getDeviceName" :searchData="searchData"
      :searchOptions="searchOptions" @getGroupSucceed="loadData"></search-bar>
    <div class="kanbanContent">
      <div class="top_content">
        <card-module title="良率推移图" cardBg="cardBg_large" titleBg="cardTitle_large" width="100%" height="100%"
          titleWidth="70%" @exportExcel="exportEchart()">
          <div style="width: 100%; height: 100%" ref="yieldChart"></div>
        </card-module>
      </div>
      <div class="bottom_content">
        <card-module title="良率报表" cardBg="cardBg_large" titleBg="cardTitle_large" width="100%" height="100%"
          titleWidth="70%" @exportExcel="exportTable">
          <div class="warn_list">
            <div class="warn_content" ref="warnContent">
              <table :key="timers" cellpadding="0" cellspacing="0" class="tableHead">
                <thead>
                  <tr>
                    <td v-for="(prop, index) in tableColumns" :key="index" :style="{ width: prop.width + 'px' }">{{
                      prop.title }}</td>
                  </tr>
                </thead>
                <tbody>
                  <tr>
                    <td v-for="(prop, colIndex) in actualTableData" :key="colIndex" :style="{ width: prop.width + 'px' }"
                      class="active">{{ prop.title }}</td>
                  </tr>
                  <tr>
                    <td v-for="(prop, colIndex) in OKTableData" :key="colIndex" :style="{ width: prop.width + 'px' }"
                      class="active">{{ prop.title }}</td>
                  </tr>
                  <tr>
                    <td v-for="(prop, colIndex) in NGTableData" :key="colIndex" :style="{ width: prop.width + 'px' }"
                      class="active">{{ prop.title }}</td>
                  </tr>
                  <tr>
                    <td v-for="(prop, colIndex) in yieldTableData" :key="colIndex" :style="{ width: prop.width + 'px' }"
                      class="active">{{ prop.title }}</td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </card-module>
      </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery';
import { fontSize, parseTime } from '@/utils/common';
import { KANBAN_CONFIG } from '@/utils/ajaxConfigure';
import CardModule from '@/components/common/kanban/CardModule';
import searchBar from './components/searchBar.vue';
import { exportJson2Excel } from '@/utils/exportExcel';
import { STATUS_CODE } from '@/utils/constantsFile';
// import { formatJson } from '@/utils/publicFunction';
import { getSearchToday } from '@/utils/publicFunction2';
import { prodChartYield } from '@/api/xwReport';
export default {
  name: 'kanban_xw_DTplato',
  components: {
    // vueSeamlessScroll,
    CardModule,
    searchBar
  },
  data() {
    return {
      timers: '',
      yieldDeviceName: '裁切检测包装机25305-1',
      title: '',
      groupId: 0,
      time: '',
      dataTime: '',
      tableHeight: 180,
      searchData: {
        groupValue: [1, 2, 3, '00000190_00000005'],
        date: [getSearchToday(6) + ' 00:00:00', getSearchToday(0) + ' 23:59:59'],
        timeKeyEnum: 'DAY'
      },
      searchOptions: [
        {
          type: 'cascader',
          key: 'groupValue'
        },
        {
          type: 'daterange',
          key: 'date',
          defaultType: '天',
          options: [
            {
              label: '小时',
              value: 'LESSON'
            },
            {
              label: '天',
              value: 'DAY'
            },
            {
              label: '年',
              value: 'MONTH'
            }
          ]
        }
      ],
      tableColumns: [],
      actualTableData: [],
      OKTableData: [],
      NGTableData: [],
      yieldTableData: [],
      kanbanConfig: { title: '安灯看板', key: '', time: 15000 },
      stateEnum: {
        yellow_off: '停用',
        red_e: '异常',
        blue_m: '保养',
        green_w: '待料',
        white_m: '维护',
        green_p: '生产'
      },
      list: [
        {
          line: 'L02',
          station: 'PCA',
          code: '01',
          startTime: '2023-03-24 09:35:24',
          endTime: '2023-03-24 09:35:24',
          time: 2,
          error: 'Idel'
        }
      ],
      yieldXData: [],
      yieldData: [],
      planYield: [],
      coatingData: {},
      barChartInfo: {
        current: 1,
        size: 4
      },
      maintainAnalyse: {
        current: 1,
        size: 4
      },
      timer: null,
      timer2: null
    };
  },
  computed: {
    optionHover() {
      return {
        step: 0.4, // 数值越大速度滚动越快
        limitMoveNum: 5, // 开始无缝滚动的数据量 this.list.length
        hoverStop: true, // 是否开启鼠标悬停stop
        direction: 1, // 0向下 1向上 2向左 3向右
        openWatch: true, // 开启数据实时监控刷新dom
        singleHeight: 0, // 单步运动停止的高度(默认值0是无缝不停止的滚动) direction => 0/1
        singleWidth: 0, // 单步运动停止的宽度(默认值0是无缝不停止的滚动) direction => 2/3
        waitTime: 1000 // 单步运动停止的时间(默认值1000ms)
      };
    }
  },
  mounted() {
    document.documentElement.style.fontSize = this.fontSize() + 'px';
    window.onresize = () => {
      document.documentElement.style.fontSize = this.fontSize() + 'px';
    };
    this.tableHeight = this.$refs.warnContent.clientHeight - 20;
    // this.loadData();
  },
  created() {
    var that = this;
    if (KANBAN_CONFIG[this.getUrlKeyMain('cid', window.location.href)]) {
      that.kanbanConfig =
        KANBAN_CONFIG[this.getUrlKeyMain('cid', window.location.href)];
    }
    this.getDataTime();
  },
  destroyed() {
    this.timer && setInterval(this.timer);
    this.timer2 && setInterval(this.timer2);
  },
  methods: {
    exportEchart() {
      const multiHeader = [];
      exportJson2Excel({
        multiHeader,
        header: ['', ...this.yieldXData],
        data: [['良品率', ...this.yieldData], ['计划良率', ...this.planYield]],
        filename: this.yieldDeviceName + '-' + '良率推移图报表',
        merges: []
      });
    },
    exportTable() {
      const multiHeader = [];
      const header = [...this.tableColumns.map(item => item.title)];
      // const filterVal = this.tableColumns.map(item => item.title);
      const data = [
        [...this.actualTableData.map(item => item.title)], [...this.OKTableData.map(item => item.title)], [...this.NGTableData.map(item => item.title)], [...this.yieldTableData.map(item => item.title + '%')]
      ];
      exportJson2Excel({
        multiHeader,
        header,
        data,
        filename: this.yieldDeviceName + '-' + '良率报表',
        merges: []
      });
    },
    getDataTime() {
      this.dataTime = new Date(
        $.ajax({ type: 'HEAD', async: false }).getResponseHeader('Date')
      ).getTime();
      this.toGetTime();
    },
    toGetTime() {
      setInterval(() => {
        var date = new Date(this.dataTime);
        var y = date.getFullYear();
        var M =
          date.getMonth() < 9
            ? '0' + (date.getMonth() + 1)
            : date.getMonth() + 1;
        var d = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
        var h = date.getHours() < 10 ? '0' + date.getHours() : date.getHours();
        var m =
          date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes();
        var s =
          date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds();
        this.time =
          y + ' 年 ' + M + ' 月 ' + d + ' 日   ' + h + ':' + m + ':' + s;
        this.dataTime += 1000;
      }, 1000);
    },
    fontSize(num = 10) {
      const clientWidth =
        window.innerWidth ||
        document.documentElement.clientWidth ||
        document.body.clientWidth;
      if (!clientWidth) return;
      const fontSize = clientWidth / 1920;
      return num * fontSize;
    },
    showLogo() {
      return 'logo_' + this.kanbanConfig.logo;
    },
    getUrlKeyMain(name, url) {
      // eslint-disable-next-line no-sparse-arrays
      return (
        decodeURIComponent(
          (new RegExp('[?|&]' + name + '=' + '([^&;]+?)(&|#|;|$)').exec(
            url
          ) || ['', ''])[1].replace(/\+/g, '%20')
        ) || null
      );
    },
    // 获取良率推移图数据
    async getYieldChartData() {
      // console.log(this.yieldDeviceName);
      try {
        const { timeKeyEnum, date, groupValue } = this.searchData;
        const param = { timeKeyEnum };
        if (date) {
          if (timeKeyEnum === 'MONTH') {
            param.startTime = parseTime(date);
            param.endTime = parseTime(date);
          } else if (timeKeyEnum === 'LESSON') {
            param.startTime = parseTime(date[0]);
            param.endTime = parseTime(date[1]);
          } else if (timeKeyEnum === 'DAY') {
            param.startTime = parseTime(date[0]);
            param.endTime = parseTime(date[1]);
          }
        } else {
          param.timeKeyEnum = 'DAY';
          param.startTime = getSearchToday(6) + ' 00:00:00';
          param.endTime = getSearchToday(0) + ' 23:59:59';
        }
        if (groupValue) {
          var str = groupValue[groupValue.length - 1];
          if (isNaN(str)) {
            param.deviceCode = str;
          } else {
            param.groupId = str;
          }
        }
        const res = await prodChartYield(param, {});
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 获得良率推移图数据 凸轮 视觉机无良率 默认设为100%
          if (this.yieldDeviceName.indexOf('凸轮') !== -1 || this.yieldDeviceName.indexOf('视觉') !== -1) {
            this.yieldXData = res.rows.map(item => item.key);
            this.yieldData = res.rows.map(item => {
              item.yield = 100;
              return item.yield;
            });
            this.planYield = res.rows.map(item => {
              item.planYield = 100;
              return item.planYield;
            });
          } else {
            this.yieldXData = res.rows.map(item => item.key);
            this.yieldData = res.rows.map(item =>
              parseFloat(item.yield?.toFixed(2) || 0)
            );
            this.planYield = res.rows.map(item => item.planYield);
            console.log(this.yieldData);
          }
          this.drawYieldChart(this.yieldXData, this.yieldData, this.planYield);
          // 由设备名称动态获得良率报表数据
          if (this.yieldDeviceName.indexOf('凸轮') !== -1 || this.yieldDeviceName.indexOf('视觉') !== -1) {
            const Date = res.rows.map(item => ({ title: item.key }));
            const tempactualQty = res.rows.map(item => ({ title: item.actualQty }));
            const newDate = [{ title: '日期' }, ...Date];
            const actualQty = [{ title: '生产数' }, ...tempactualQty];
            this.tableColumns = newDate;
            this.actualTableData = actualQty;
          } else {
            const Date = res.rows.map(item => ({ title: item.key }));
            const tempactualQty = res.rows.map(item => ({ title: item.actualQty }));
            const tempokQty = res.rows.map(item => ({ title: item.okQty }));
            const tempngQty = res.rows.map(item => ({ title: item.ngQty }));
            const tempyield = res.rows.map(item => ({ title: parseFloat(item.yield?.toFixed(2) || 0) }));

            const newDate = [{ title: '日期' }, ...Date];
            const actualQty = [{ title: '生产数' }, ...tempactualQty];
            const okQty = [{ title: 'OK数' }, ...tempokQty];
            const ngQty = [{ title: 'NG数' }, ...tempngQty];
            const Yield = [{ title: '良率' }, ...tempyield];

            this.tableColumns = newDate;
            this.actualTableData = actualQty;
            this.OKTableData = okQty;
            this.NGTableData = ngQty;
            this.yieldTableData = Yield;
          }
        } else {
          this.yieldXData = [];
          this.yieldData = [];
          this.planYield = [];
          this.drawYieldChart(this.yieldXData, this.yieldData, this.planYield);
        }
      } catch (error) {
        console.log(error);
      }
    },
    // 画良率推移图
    drawYieldChart(xData, seriesData, planYield) {
      const YieldChart = this.$echarts.init(this.$refs.yieldChart);
      var BZ = '';
      if (this.yieldDeviceName.indexOf('凸轮') !== -1) {
        BZ = '凸轮机无良品率 默认设为100%';
      } else if (this.yieldDeviceName.indexOf('视觉') !== -1) {
        BZ = '视觉机无良品率 默认设为100%';
      }
      const option = {
        // color: EQUIPMENT_STATUS_REPORT.COLOR,
        title: {
          text: BZ,
          textStyle: {
            color: '#fff',
            fontSize: 10
          },
          x: 'center',
          padding: [1, 0],
          left: '80%'
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            // 坐标轴指示器，坐标轴触发有效
            type: 'line' // 默认为直线，可选为：'line' | 'shadow'
          },
          formatter(params) {
            var relVal = params[0].name;
            for (var i = 0, l = params.length; i < l; i++) {
              // 遍历出来的值一般是字符串，需要转换成数字，再进项toFixed四舍五入
              relVal +=
                '<br/>' +
                params[i].marker +
                params[i].seriesName +
                ' : ' +
                Number(params[i].value).toFixed(2) +
                '%';
            }
            return relVal;
          }
        },
        legend: {
          show: true,
          top: '0%',
          textStyle: {
            color: '#ffffff',
            fontSize: fontSize(16)
          }
        },
        // barMaxWidth: '60%',
        grid: {
          top: '10%',
          left: '3%',
          right: '3%',
          bottom: '7%',
          containLabel: true
        },
        xAxis: [
          {
            type: 'category',
            name: '日期',
            data: xData,
            axisTick: {
              alignWithLabel: true
            },
            axisLine: {
              lineStyle: {
                color: '#929EE6'
              }
            },
            axisLabel: {
              fontSize: fontSize(18),
              color: '#D8D8D8'
            },
            nameTextStyle: {
              fontSize: fontSize(18)
            },
            nameGap: '5'
          }
        ],
        yAxis: [
          {
            type: 'value',
            name: '良品率',
            min: 0,
            // minInterval: 1,
            // splitNumber: 0.2,
            axisLine: {
              lineStyle: {
                color: '#929EE6'
              }
            },
            axisLabel: {
              formatter: '{value}%',
              fontSize: fontSize(18),
              color: '#ffffff'
            },
            nameTextStyle: {
              fontSize: fontSize(18)
            },
            splitLine: {
              show: true,
              lineStyle: {
                type: 'dashed',
                color: '#24ffff',
                opacity: 0.5
              }
            }
          }
        ],
        series: [
          {
            name: '良品率',
            type: 'bar',
            smooth: true,
            showAllSymbol: true,
            symbol: 'circle',
            symbolSize: fontSize(12),
            itemStyle: {
              color: '#058cff'
            },
            lineStyle: {
              width: fontSize(5),
              color: '#058cff',
              shadowColor: 'rgba(158,135,255, 0.3)',
              shadowBlur: 10,
              shadowOffsetY: 20
            },
            label: {
              normal: {
                show: true,
                formatter: function(params) {
                  // 标签内容
                  return params.value + '%';
                },
                position: 'top',
                fontSize: fontSize(18),
                color: '#ffffff'
              }
            },
            // areaStyle: {
            //   color: 'rgba(5,140,255, 0.2)'
            // },
            data: seriesData
          },
          {
            name: '计划良率',
            type: 'line',
            // yAxisIndex: 1,
            // smooth: true,
            showAllSymbol: true,
            symbol: 'circle',
            symbolSize: 10,
            itemStyle: {
              color: '#09dba0'
            },
            lineStyle: {
              color: '#09dba0'
            },
            areaStyle: {
              color: 'rgba(5,140,255, 0.2)'
            },
            data: planYield
          }
        ]
      };
      YieldChart.setOption(option, true);
      window.addEventListener('resize', () => {
        YieldChart.resize();
      });
    },
    search() {
      console.log(this.searchData);
      this.getYieldChartData();
    },
    // 获取子组件传递过来的设备名称
    getDeviceName(deviceName) {
      this.yieldDeviceName = deviceName;
      if (deviceName.indexOf('凸轮') !== -1 || deviceName.indexOf('视觉') !== -1) {
        this.actualTableData = [{ title: '生产数' }];
        this.OKTableData = [];
        this.NGTableData = [];
        this.yieldTableData = [];
      } else {
        this.actualTableData = [{ title: '生产数' }];
        this.OKTableData = [{ title: 'OK数' }];
        this.NGTableData = [{ title: 'NG数' }];
        this.yieldTableData = [{ title: '良率' }];
      }
      this.timers = new Date().getTime();// 根据时间戳不同动态加载Table组件
    },
    loadData() {
      this.getYieldChartData();
    }
  }
};
</script>

