<template>
  <div class="search_content">
    <el-select @change="selectPage" v-if="isShowSelectPage" v-model="pagePath" placeholder="请选择报表" class="commonSelect m-r10">
      <el-option v-for="item in pageList" :key="item.value" :label="item.label" :value="item.value">
      </el-option>
    </el-select>
    <div class="item_condition" v-for="item in searchOptions" :key="item.key">
      <span v-if="item.name && item.type === 'cascader'">车间: </span>
      <el-cascader ref="demoCascader" @change="transmitYieldName" v-if="item.type === 'cascader'" :options="groupOptions"
        v-model="searchData[item.key]" :show-all-levels="false" filterable :props="{ checkStrictly: true, expandTrigger: 'hover' }"
        class="formItem_full"></el-cascader>

      <el-select @change="val => transmitName(val, item.options)" v-if="item.type === 'select'" v-model="searchData[item.key]" placeholder="请选择"
        class="commonSelect">
        <el-option v-for="option in item.options" :key="option.value" :label="option.label" :value="option.value">
        </el-option>
      </el-select>

      <span v-if="item.name && item.type === 'daterange'">时间: </span>
      <el-select v-model="searchData['timeKeyEnum']" placeholder="请选择" class="timeType" v-if="item.type === 'daterange'">
        <el-option v-for="option in item.options" :key="option.value" :label="option.label" :value="option.value"  :disabled="option.disabled">
        </el-option>
      </el-select>
      <el-date-picker v-show="searchData['timeKeyEnum'] === 'MONTH' && item.type === 'daterange'"
        v-model="searchData[item.key]" type="year" placeholder="请选择"></el-date-picker>
      <!-- <el-date-picker
        v-show="searchData['timeKeyEnum'] === '月' && item.type === 'daterange'"
        v-model="searchData[item.key]"
        type="month"
        placeholder="请选择"
      >
      </el-date-picker> -->
      <el-date-picker v-show="searchData['timeKeyEnum'] === 'DAY' && item.type === 'daterange'"
        v-model="searchData[item.key]" type="daterange" range-separator="至" start-placeholder="开始日期"
        end-placeholder="结束日期">
      </el-date-picker>
      <el-date-picker v-show="searchData['timeKeyEnum'] === 'HOUR' && item.type === 'daterange'"
        v-model="searchData[item.key]" type="datetimerange" range-separator="至" start-placeholder="开始日期"
        end-placeholder="结束日期">
      </el-date-picker>
      <el-date-picker v-show="searchData['timeKeyEnum'] === 'LESSON' && item.type === 'daterange'"
        v-model="searchData[item.key]" type="datetimerange" range-separator="至" start-placeholder="开始日期"
        end-placeholder="结束日期">
      </el-date-picker>
      <el-date-picker v-show="searchData['timeKeyEnum'] === 'DAY' && item.type === 'date'" v-model="searchData[item.key]"
        type="date" placeholder="选择日期">
      </el-date-picker>
    </div>
    <el-button type="primary" @click="search">查询</el-button>
    <el-button class="backBtn" @click="back" v-if="isShowSelectPage">返回</el-button>
  </div>
</template>
<script>
import { deviceWorkshopLineDeviceGetTreeFunc, deviceWorkshopTypeGetTreeFunc } from '@/utils/publicFunction';
import { STATUS_CODE } from '@/utils/constantsFile';
export default {
  name: 'searchBar',
  props: {
    searchData: {
      type: Object,
      default: () => ({})
    },
    searchOptions: {
      type: Array,
      default: () => []
    },
    groupSearchType: {
      type: Number,
      default: 1
    }
  },
  data() {
    return {
      groupValue: '',
      timeType: '天',
      year: '',
      day: '',
      groupOptions: [],
      deviceCode: '',
      deviceName: '',
      pagePath: '',
      isShowSelectPage: false,
      pageList: [
        {
          label: 'OEE推移图',
          value: '/xwreport/oee'
        },
        {
          label: '产能推移图',
          value: '/xwreport/capacity'
        },
        {
          label: '当机推移图',
          value: '/xwreport/currentCapacity'
        },
        {
          label: 'DT柏拉图及明细',
          value: '/xwreport/DTplato'
        },
        {
          label: '良率报表',
          value: '/xwreport/yieldReport'
        },
        {
          label: 'DT改善决策及水平展开',
          value: '/xwreport/troubleImprove'
        },
        {
          label: 'OEE报表',
          value: '/xwreport/currentDetail'
        }
      ]
    };
  },
  created() {
    this.pathname = location.pathname;
    const params = new URLSearchParams(location.search);
    this.isShowSelectPage = params.has('deviceCode');
    this.deviceCode = params.get('deviceCode');
    this.deviceName = params.get('deviceName');
    this.getGroupData();
  },
  methods: {
    back() {
      this.$router.back();
      // this.$router.push({ path: '/xwreport/imp6', query: { cid: 190, tvNo: 6 } });
    },
    selectPage(path) {
      this.$router.replace({ path, query: { deviceCode: this.deviceCode, deviceName: this.deviceName } });
    },
    // 子组件向当机明细报表组件传设备名称
    transmitName(deviceCode, list) {
      const deviceName = list.filter(item => item.value === deviceCode)[0].label;
      this.$emit('transmitName', deviceName);
    },
    // 子组件向其他报表传设备名称
    transmitYieldName() {
      const deviceName = this.$refs.demoCascader[0].getCheckedNodes()[0].label;
      this.$refs.demoCascader[0].dropDownVisible = false;
      this.$emit('transmitYieldName', deviceName);
    },
    search() {
      this.$emit('search');
    },
    getGroupData() {
      if (this.groupSearchType === 1) { // 获取 车间 —— 产线 —— 设备
        this.getGroupLineDevice();
      } else if (this.groupSearchType === 2) { // 获取 车间 —— 产线
        this.getGroupLine();
      }
    },
    // 获取 车间 —— 产线 —— 设备
    async getGroupLineDevice() {
      try {
        var param = {};
        const res = await deviceWorkshopLineDeviceGetTreeFunc(param, {});
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.groupOptions = res.rows;
          if (this.deviceCode && this.pathname !== '/xwreport/currentDetail') {
            this.searchData.groupValue = this.getParentsById(this.groupOptions, this.deviceCode);
            this.$nextTick(() => {
              const deviceName = this.$refs.demoCascader[0].getCheckedNodes()[0].label;
              this.$emit('transmitYieldName', deviceName);
            });
          }
          this.$emit('getGroupSucceed');
        }
      } catch (error) {
        console.log(error);
      }
    },
    // 获取 车间 —— 产线
    async getGroupLine() {
      try {
        var param = {};
        const res = await deviceWorkshopTypeGetTreeFunc(param, {});
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.groupOptions = res.rows;
        }
      } catch (error) {
        console.log(error);
      }
    },
    getParentsById(list, id) {
      for (const i in list) {
        if (list[i].value === id) {
          return [list[i].value];
        }
        if (list[i].children) {
          const node = this.getParentsById(list[i].children, id);
          if (node !== undefined) {
            node.unshift(list[i].value);
            return node;
          }
        }
      }
    }
  }
};
</script>
