<template>
  <div class="cardModule" :style="{ height: height, width: width }">
    <div :class="['wrapper', cardBg]">
      <div :class="['title', titleBg]" :style="{ width: titleWidth }">{{ title }}</div>
      <div class="module_content">
        <div class="export_btn" v-if="isExport" @click="exportExcel">
          <i slot="operation-icon" class="icon-export"></i>
          <span>导出</span>
        </div>
        <slot></slot>
      </div>
      <div class="bottonLine"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'CardModule',
  props: {
    title: String,
    width: {
      width: String,
      default: '100%'
    },
    height: {
      type: String,
      default: '66.66%'
    },
    titleWidth: {
      type: String,
      default: '60%'
    },
    cardBg: {
      type: String,
      default: 'cardBg_small'
    },
    titleBg: {
      type: String,
      default: 'cardTitle_small'
    },
    isExport: {
      type: Boolean,
      default: true
    }
  },
  computed: {

  },
  data() {
    return {
    };
  },
  methods: {
    exportExcel() {
      this.$emit('exportExcel');
    }
  }
};
</script>

