      height: calc(100% - (40 / 1080) * 100vh);

      .export_btn {
        display: flex;
        align-items: center;
        position: absolute;
        top: -35px;
        right: 25px;
        cursor: pointer;
      }
    }
  }

  .title {
    // width: 30vw;
    // width: 60%;
    // height: 4rem;
    // line-height: 4rem;
    height: 40px;
    line-height: 40px;
    color: #fff;
    // font-size: 1.7rem;
    padding-left: 40px;
    // padding: 0.8rem 0 0.5rem 5rem;
    background-size: 100% 100%;
  }

  .bottonLine {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 1rem;
    background: url('~@/assets/img/deviceManage/cardLine.png') no-repeat;
    background-size: 100% 100%;
  }

  .cardBg_small {
    background: url('~@/assets/img/deviceManage/cardBg_small.png') no-repeat;
